.footer {
  /* background: var(--color-gray); */
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100px;
}
.footer-links {
  display: flex;
  align-items: center;
}
.footer-links a {
  color: black;
  opacity: 0.15;
  text-decoration: none;
  font-size: 24px;
  padding: 0px 10px;
}
.footer-links a:hover {
  opacity: 0.5;
}
/* .footer p .heart {
  color: #d23333;
} */

.footer-copyright,
.footer-copyright span {
  color: rgba(0, 0, 0, 0.3);
}

.footer-copyright .bi-heart-fill {
  color: #d23333;
}
